import React from 'react';
import {Header} from "../../components/header";
import {Slider} from "../../components/slider";
import {Card} from "../../components/card";
import Button from "../../elements/btn";
import {slides} from "../../temp";
import {services} from "../../temp";
import {Swiper, SwiperSlide} from 'swiper/react';
import {Autoplay} from 'swiper';
import 'swiper/css';
import './styles.scss';
import arrow from '../../img/totop.svg'
import slide1 from '../../img/laptop.png'
import slide2 from '../../img/slide2.jpg'
import slide3 from '../../img/slide3.jpg'
import strp from '../../img/rectangle.png'

export const Home = () => {
    const cardsJSX = services.map(item => {
        return <Card content={item}/>
    })
    const goTop = () => {
        window.scrollTo({behavior: 'smooth', top: 0})
    }

    return (
      <>
          <Header/>
          <main>
              <div className="top" onClick={goTop}>
                  <img src={arrow} alt=""/>
                  to top
              </div>
              <div className="land-wrapp">
                  <section className="land" id={'about'}>
                      <div className="articles">
                          <Slider slides={slides}/>
                      </div>
                  </section>
                  <div className="swiper-cont">
                      <div className="strp">
                          <img src={strp} alt=""/>
                      </div>

                      <Swiper
                        direction={'vertical'}
                        modules={[Autoplay]}
                        autoplay={{
                            delay: 3000,
                            loop: true,
                            infinite: true
                        }}
                        slidesPerView={1}
                      >
                          <SwiperSlide>
                              <img src={slide1} alt=""/>
                          </SwiperSlide>
                          <SwiperSlide>
                              <img src={slide2} alt=""/>
                          </SwiperSlide>
                          <SwiperSlide>
                              <img src={slide3} alt=""/>
                          </SwiperSlide>
                      </Swiper>

                  </div>
              </div>
              <section id="services">
                  <div className={'list'}>
                      {services.map(item => (<Card content={item} key={Math.floor(Math.random() * 1000)}/>))}
                  </div>
                  <Button className={'square'}> </Button>
              </section>
              <div id="contacts">
                  <div className="bg"></div>
                  <section>
                      <header>
                          <h3></h3>
                          <h4> </h4>
                          <p>  -01</p>
                      </header>
                      <div className="content">
                          <p>
                                   .          ,     .  6-     -,  , ,    
                                    .
                          </p>
                          <p>
                                         .        ,                
                              
                                .  ,          CRM        Swipex  Skype,  .
                          </p>
                          <p>
                                   ,     ,                .  Swipex         , 
                              
                                 ,         .
                          </p>
                      </div>
                  </section>
              </div>
          </main>
      </>
    )
}
